<script>
import { Line } from "vue-chartjs";

export default {
  extends: Line,
  data() {
    return {
      gradient: null,
      gradient2: null,
      xLabel: [],
      yData: [],
    };
  },
  props: {
    statusByWeek: Array,
  },
  watch: {
    statusByWeek() {
      for (let day of this.statusByWeek) {
        this.xLabel.push(day.title);
        this.yData.push({ stepCount: day.stepCount, y: day.distance });
      }
      this.chartInit();
    },
  },
  methods: {
    chartInit() {
      this.gradient = this.$refs.canvas
        .getContext("2d")
        .createLinearGradient(0, 0, 0, 450);
      this.gradient2 = this.$refs.canvas
        .getContext("2d")
        .createLinearGradient(0, 0, 0, 450);
      if (this.$env == "development") console.log(this.xLabel);
      if (this.$env == "development") console.log(this.yData);

      this.gradient.addColorStop(0, "rgba(0, 122, 241, 0.5)");
      this.gradient.addColorStop(0.5, "rgba(0, 122, 241, 0.25)");
      this.gradient.addColorStop(1, "rgba(0, 122, 241, 0)");

      this.gradient2.addColorStop(0, "rgba(0, 231, 255, 0.9)");
      this.gradient2.addColorStop(0.5, "rgba(0, 231, 255, 0.25)");
      this.gradient2.addColorStop(1, "rgba(0, 231, 255, 0)");

      this.renderChart(
        {
          labels: this.xLabel,
          datasets: [
            {
              label: " ",
              borderColor: "#007AF1",
              pointBackgroundColor: "#007AF1",
              borderWidth: 1,
              pointBorderColor: "white",
              backgroundColor: this.gradient,
              data: this.yData,
            },
          ],
        },
        {
          responsive: true,
          maintainAspectRatio: false,
          legend: {
            display: false,
          },
          scales: {
            xAxes: [
              {
                gridLines: {
                  display: false,
                },
                ticks: {
                  callback: function (value) {
                    return value;
                  },
                  autoSkip: true,
                  maxTicksLimit: 7,
                  stepSize: 0.2,
                  maxRotation: 0,
                  minRotation: 0,
                },
                scaleLabel: {
                  display: true,
                  labelString: "",
                  fontStyle: "bold",
                },
              },
            ],
            yAxes: [
              {
                display: true,
                ticks: {
                  suggestedMin: 0, // minimum will be 0, unless there is a lower value.
                  // OR //
                  beginAtZero: true, // minimum value will be 0.
                },
              },
            ],
          },
          tooltips: {
            callbacks: {
              label: function (tooltipItem, data) {
                const idx = tooltipItem.index;
                const walk = data.datasets[0].data[idx].stepCount;
                const km = data.datasets[0].data[idx].y;
                return `${km}km, ${walk}`;
              },
              title: function (data) {
                return `${data[0].xLabel}`;
              },
            },
          },
        }
      );
    },
  },
  mounted() {},
};
</script>
