<template>
  <div class="svg-item">
    <svg width="100%" height="100%" viewBox="0 0 40 40" class="donut">
      <defs>
        <linearGradient id="gradient" x1="0%" y1="0%" x2="0%" y2="100%">
          <stop offset="0%" stop-color="#27c4ba" />
          <stop offset="100%" stop-color="#4d8fb5" />
        </linearGradient>
      </defs>
      <circle
        class="donut-hole"
        cx="20"
        cy="20"
        r="15.91549430918954"
        fill="#fff"
      ></circle>
      <circle
        class="donut-ring"
        cx="20"
        cy="20"
        r="15.91549430918954"
        fill="transparent"
        stroke-width="3.5"
      ></circle>
      <circle
        class="donut-segment-2"
        ref="el"
        cx="20"
        cy="20"
        r="15.91549430918954"
        fill="transparent"
        stroke="url(#gradient)"
        stroke-linecap="round"
        stroke-width="3.5"
        stroke-dashoffset="25"
      ></circle>
      <g>
        <text y="50%" transform="translate(0, 2)">
          <tspan x="50%" text-anchor="middle" class="donut-percent">
            {{ educationProgress }}
            <tspan>%</tspan>
          </tspan>
        </text>
      </g>
    </svg>
  </div>
</template>
<style>
.svg-item {
  width: 100%;
  font-size: 16px;
  margin: 0 auto;
  animation: donutfade 1s;
  width: 45%;
}

@keyframes donutfade {
  /* this applies to the whole svg item wrapper */
  0% {
    opacity: 0.2;
  }
  100% {
    opacity: 1;
  }
}

.donut-ring {
  stroke: #ebebeb;
}

.donut-segment-2 {
  animation: donut 3s;
}

.donut-percent {
  animation: donutfadelong 1s;
}

/* @keyframes donutfadelong {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
} */

.donut-label {
  font-size: 0.28em;
  font-weight: 700;
  line-height: 1;
  fill: #000;
  transform: translateY(0.25em);
}

.donut-percent {
  font-size: 0.5em;
  line-height: 1;
  transform: translateY(0.5em);
  font-weight: bold;
}
/* 
.donut-data {
  font-size: 0.12em;
  line-height: 1;
  transform: translateY(0.5em);
  text-align: center;
  text-anchor: middle;
  color: #666;
  fill: #666;
  animation: donutfadelong 1s;
} */

/* ---------- */
/* just for this presentation */
.svg-item {
  display: inline-block;
}
</style>
<script>
export default {
  props: {
    educationProgress: Number,
  },
  mounted() {
    const text = `${this.educationProgress}, ${100 - this.educationProgress}`;
    this.$refs.el.setAttribute("stroke-dasharray", text);
  },
};
</script>
