<template>
  <div class="page__wrap">
    <main class="treatment__status__main">
      <PrintBox ref="printBox" />
      <TreatmentPreviewPopup
        class="none__print"
        :popupEventFlag="previewPopFlag"
        @treatmentPreviewPopClose="treatmentPreviewPopClose"
        :treatmentInfo="treatmentPreview"
      />
      <MStrongPopup
        class="none__print"
        :popupEventFlag="mStrongPopupEventFlag"
        @popupClose="toggleMStrongPopup"
        @runPrint="runPrint"
        @mStrongPublish="mStrongPublish"
      />
      <PatientProfile
        ref="patientProfile"
        class="none__print"
        @toggleMStrongPopup="toggleMStrongPopup"
        @isLoadingSet="isLoadingSet"
        :endDate="endDate"
        :startDate="startDate"
        :isLoading="isLoading"
      />
      <section class="status__box box__shadow__01 none__print">
        <div class="page__section__state__wrap">
          <div class="inner">
            <div class="button__wrap">
              <button
                class="custom__btn"
                :class="{ off: contentState === 'Treatment' }"
                @click="stateContentsChange('Education')"
                ref="educationBtn"
              >
                <p></p>
              </button>
              <button
                class="custom__btn"
                :class="{ off: contentState === 'Education' }"
                @click="stateContentsChange('Treatment')"
                ref="treatmentBtn"
              >
                <p></p>
              </button>
            </div>
          </div>
        </div>
        <div
          class="contents__train"
          ref="contentsActiveBox"
          :class="{
            treatment: contentState === 'Treatment',
            education: contentState === 'Education',
          }"
        >
          <EducationStatus
            ref="educationStatusRef"
            :resDashboard="resDashboard"
            @treatmentPreview="treatmentPreviewPopOpen"
            @treatmentPreviewLoading="treatmentPreviewLoading"
          />
          <TreatmentStatus
            ref="treatmentStatusRef"
            :subjectName="subjectName"
          />
        </div>
      </section>
    </main>
    <div class="popup__wrap none__print" :class="{ on: isLoading2 }">
      <div class="popup__bg">
        <LoadingBar class="loading" />
      </div>
    </div>
  </div>
</template>
<script>
import "@/assets/css/treatmentStatus.css";
import "@/assets/css/print.css";
import TreatmentStatus from "@/components/PatientDashboard/TreatmentStatus";
import EducationStatus from "@/components/PatientDashboard/EducationStatus";
import PatientProfile from "@/components/PatientDashboard/PatientProfile";
import MStrongPopup from "@/components/PatientInquiry/MStrongPrescriptionPopup";
import TreatmentPreviewPopup from "@/components/PrescriptionCom/TreatmentPreviewPopup";
import LoadingBar from "@/components/common/LoadingBar";
import data7 from "@/data/patientManage/data7.json";
import data8 from "@/data/patientManage/data8.json";
import data9 from "@/data/patientManage/data9.json";
import PrintBox from "@/components/PrescriptionCom/PrintBox";

export default {
  components: {
    TreatmentStatus,
    EducationStatus,
    PatientProfile,
    MStrongPopup,
    TreatmentPreviewPopup,
    LoadingBar,
    PrintBox,
  },
  data() {
    return {
      contentState: "Education",
      mStrongPopupEventFlag: "Close",
      endDate: "-",
      startDate: "-",
      isLoading: true,
      isLoading2: false,
      subjectName: "",
      previewPopFlag: false,
      treatmentPreview: { name: "" },
      resDashboard: {},
    };
  },
  methods: {
    isLoadingSet(param) {
      this.isLoading = param;
    },
    runPrint() {
      window.print();
    },
    treatmentPreviewLoading(param) {
      this.isLoading2 = param;
    },
    stateContentsChange(params) {
      this.contentState = params;
    },
    toggleMStrongPopup() {
      this.mStrongPopupEventFlag === "Close"
        ? (this.mStrongPopupEventFlag = "Open")
        : (this.mStrongPopupEventFlag = "Close");
    },
    treatmentPreviewPopOpen(param) {
      this.previewPopFlag = true;
      Object.assign(this.treatmentPreview, param);
    },
    treatmentPreviewPopClose() {
      this.previewPopFlag = false;
    },
    pageInit() {
      if (Object.keys(this.$route.params).length === 0) {
        this.$router.push({
          name: "PatientInquiry",
        });
      } else {
        const accessCode = this.$route.params.accessCode;
        // const patientRegNumber = this.$route.params.patientRegNumber;
        this.subjectName = this.$route.params.subjectName;
        if (
          accessCode !== "" &&
          accessCode !== null &&
          accessCode !== undefined
        ) {
          //   
          let startDate = this.$route.params.startDate;
          let dateFlag = false;

          if (
            startDate !== null &&
            startDate !== "" &&
            startDate !== undefined
          ) {
            dateFlag =
              new Date(
                startDate.substring(0, 4),
                Number(startDate.substring(4, 6)) - 1,
                startDate[6] + startDate[startDate.length - 1]
              ) <= new Date();
          }
          if (this.$route.params.subjectId === "APP002") {
            //
          } else if (this.$route.params.subjectId === "APP001") {
            if (this.$route.params.issuedYn === "Y" && dateFlag) {
              const aStopStatus = data9;
              console.log(aStopStatus);
              this.$refs.treatmentStatusRef.setAstopTreatmentStatus(
                aStopStatus
              );
            }
          }
          //   
          this.isLoading = true;
          this.$refs.patientProfile.setEducationChartFlag();

          const response = data7;
          console.log(response);
          let prescriptionLength = response.data.response.length;
          if (prescriptionLength > 0) {
            //    
            const resObj = response.data.response[0];
            if (resObj.endDate !== null) {
              this.$route.params.accessPassword = resObj.accessPassword;
              this.$route.params.startLevel = resObj.startLevel;
              this.$route.params.stretchingLevel = resObj.stretchingLevel;
              this.$route.params.walkingLevel = resObj.walkingLevel;
              this.$route.params.currentLevel = resObj.currentLevel;
              this.$route.params.endDate = resObj.endDate;
              this.$route.params.startDate = resObj.startDate;
              this.endDate = resObj.endDate;
              this.startDate = resObj.startDate;
            } else {
              this.$refs.patientProfile.setEducationChartFlag2();
            }
            this.$route.params.prescriptionListByWeek =
              resObj.prescriptionListByWeek;
            this.$refs.educationStatusRef.setEucationStatus(
              this.$route.params.prescriptionListByWeek
            );
            //    
          }
          this.$refs.printBox.setData(this.$route.params, "");

          const resDashboard = data8;
          this.resDashboard = resDashboard;
          this.$refs.patientProfile.setPieChart(resDashboard.statusByWeek);
        } else {
          this.isLoading = false;
        }
      }
    },
    mStrongPublish() {
      this.pageInit();
    },
  },
  mounted() {
    this.pageInit();
  },
};
</script>
