<template>
  <section>
    <div class="patient__profile__box first box__shadow__01">
      <!-- 1  -->
      <ul>
        <li>
          <dl>
            <dt></dt>
            <dd>{{ this.$route.params.patientRegNumber }}</dd>
          </dl>
        </li>
        <li>
          <dl>
            <dt></dt>
            <dd>
              {{
                this.$route.params.firstTreatDate.replace(
                  /(\d{4})(\d{2})(\d{2})/,
                  "$1-$2-$3"
                )
              }}
            </dd>
          </dl>
        </li>
      </ul>
      <!-- // 1  -->
      <!-- 2  -->
      <div class="ani__dount__chart">
        <ProcessChart v-if="progressChartFlag" :educationProgress="25" />
        <LoadingBar v-if="!progressChartFlag" />
        <span>(%)</span>
        <dl>
          <dt>{{ this.$route.params.name }}</dt>
          <dd>   112</dd>
        </dl>
      </div>
      <!-- // 2  -->
      <!-- 3  -->
      <ul>
        <li>
          <dl>
            <dt></dt>
            <dd>{{ this.$route.params.age }}</dd>
          </dl>
        </li>
        <li>
          <dl>
            <dt></dt>
            <dd>{{ this.$route.params.height }}cm</dd>
          </dl>
        </li>
        <li>
          <dl>
            <dt></dt>
            <dd>{{ this.$route.params.weight }}kg</dd>
          </dl>
        </li>
      </ul>
      <!-- // 3  -->
      <div class="loading__wrap">
        <LoadingBar v-if="isLoading" />
        <button
          class="custom__btn"
          v-if="!isLoading"
          @click="prescriptionBtnClick"
        >
          <p></p>
        </button>
      </div>
    </div>
    <div class="patient__profile__box second box__shadow__01">
      <DonutChart
        v-if="donutChartFlag"
        id="donut"
        :data="donutData"
        colors='[ "#089BAB", "#FC9F5B", "#F26361" ]'
        resize="true"
      ></DonutChart>
      <ul>
        <li>
          <dl>
            <dt> </dt>
            <dd>{{ this.$route.params.subjectName }}</dd>
          </dl>
        </li>
        <li>
          <dl>
            <dt> </dt>
            <dd>
              {{
                startDate === "-"
                  ? "-"
                  : startDate.replace(/(\d{4})(\d{2})(\d{2})/, "$1-$2-$3")
              }}
            </dd>
          </dl>
        </li>
        <li>
          <dl>
            <dt> </dt>
            <dd>
              {{
                endDate === "-"
                  ? "-"
                  : endDate.replace(/(\d{4})(\d{2})(\d{2})/, "$1-$2-$3")
              }}
            </dd>
          </dl>
        </li>
        <li>
          <dl>
            <dt>Access code</dt>
            <dd>
              {{
                this.$route.params.accessCode === null
                  ? "-"
                  : this.$route.params.accessCode
              }}
            </dd>
          </dl>
        </li>
        <li>
          <dl>
            <dt></dt>
            <dd>
              {{
                this.$route.params.contact === ""
                  ? "-"
                  : this.$route.params.contact.replace(
                      /(\d{3})(\d{4})(\d{4})/,
                      "$1-$2-$3"
                    )
              }}
            </dd>
          </dl>
        </li>
      </ul>
      <dl>
        <dt></dt>
        <dd>
          <textarea
            readonly
            class="read__only"
            :value="this.$route.params.memo"
          ></textarea>
        </dd>
      </dl>
    </div>
  </section>
</template>
<script>
import ProcessChart from "@/components/PatientDashboard/DountChart";
import { DonutChart } from "vue-morris";
import LoadingBar from "@/components/common/LoadingBar";

export default {
  components: {
    DonutChart,
    ProcessChart,
    LoadingBar,
  },
  props: {
    endDate: String,
    startDate: String,
    isLoading: Boolean,
  },
  watch: {
    startDate() {
      if (this.$env == "development") console.log(this.startDate);
      if (this.startDate !== "-") {
        let parseStartDate = this.startDate.replace(
          /(\d{4})(\d{2})(\d{2})/,
          "$1-$2-$3"
        );
        let startDateArr = parseStartDate.split("-");
        let stDate = new Date(
          startDateArr[0],
          startDateArr[1],
          startDateArr[2]
        );
        let nd = new Date();
        let td = new Date(nd.getFullYear(), nd.getMonth() + 1, nd.getDate());

        let btMs = td.getTime() - stDate.getTime();
        let btDay = btMs / (1000 * 60 * 60 * 24);
        this.educationProgress = Math.round((btDay / 84) * 100);
        if (this.educationProgress < 0) {
          this.educationProgress = 0;
        }
        this.progressChartFlag = true;
      }
    },
  },
  data() {
    return {
      donutData: [
        // { label: "", value: 0 },
        // { label: "", value: 0 },
        // { label: "", value: 0 },
        // { label: "", value: 0 },
      ],
      donutChartFlag: false,
      progressChartFlag: true,
      educationProgress: 0,
    };
  },
  methods: {
    setEducationChartFlag() {
      this.progressChartFlag = false;
    },
    setEducationChartFlag2() {
      this.progressChartFlag = true;
    },
    setPieChart(param) {
      this.$emit("isLoadingSet", true);
      if (param.length > 0) {
        let donutLabel = ["", "", ""];
        let donutData = [0, 0, 0];

        for (let i of param) {
          for (let j of i.days) {
            donutData[0] = donutData[0] + 1;
            let status = j.status;
            if (status == 1) {
              donutData[1] = donutData[1] + 1;
            }
            // else if (status == 2 || status == 3) {
            //   donutData[2] = donutData[2] + 1;
            // }
            else if (status == 4) {
              donutData[2] = donutData[2] + 1;
            }
          }
        }

        for (let i = 0; i < donutData.length; i++) {
          // if (donutData[i] > 0) {
          this.donutData.push({ label: donutLabel[i], value: donutData[i] });
          // }
        }
        this.donutChartFlag = true;
        this.$emit("isLoadingSet", false);
      } else {
        this.$emit("isLoadingSet", false);
      }
    },
    prescriptionBtnClick() {
      let queryParam = this.$route.params;
      if (this.$env == "development") console.log(queryParam);
      if (queryParam.subjectName === " ") {
        if (queryParam.accessCode === null) {
          this.$route.params.mStrongPrescritionState = "";
        } else {
          switch (queryParam.issuedYn) {
            case "N":
              this.$route.params.mStrongPrescritionState = "";
              break;
            case "Y":
              this.$route.params.mStrongPrescritionState = "";
              break;
          }
        }
        this.$emit("toggleMStrongPopup", "Open");
      } else {
        this.$router.push({
          name: "Prescription",
          params: this.$route.params,
        });
      }
    },
  },
};
</script>
