<script>
import { Line } from "vue-chartjs";

export default {
  extends: Line,
  data() {
    return {
      gradient: null,
      gradient2: null,
      xLabel: [],
      yData: [],
      cravingStatusList: [
        {
          status: 4,
          treatmentDt: "20230117",
        },
        {
          status: 4,
          treatmentDt: "20230118",
        },
        {
          status: 4,
          treatmentDt: "20230119",
        },
        {
          status: 0,
          treatmentDt: "20230120",
        },
        {
          status: 0,
          treatmentDt: "20230121",
        },
        {
          status: 0,
          treatmentDt: "20230122",
        },
        {
          status: 0,
          treatmentDt: "20230123",
        },
        {
          status: 0,
          treatmentDt: "20230124",
        },
        {
          status: 3,
          treatmentDt: "20230125",
        },
        {
          status: 0,
          treatmentDt: "20230126",
        },
        {
          status: 2,
          treatmentDt: "20230127",
        },
        {
          status: 0,
          treatmentDt: "20230128",
        },
        {
          status: 0,
          treatmentDt: "20230129",
        },
        {
          status: 0,
          treatmentDt: "20230130",
        },
        {
          status: 0,
          treatmentDt: "20230131",
        },
        {
          status: 0,
          treatmentDt: "20230201",
        },
        {
          status: 0,
          treatmentDt: "20230202",
        },
        {
          status: 0,
          treatmentDt: "20230203",
        },
        {
          status: 0,
          treatmentDt: "20230204",
        },
        {
          status: 0,
          treatmentDt: "20230205",
        },
        {
          status: 0,
          treatmentDt: "20230206",
        },
        {
          status: 0,
          treatmentDt: "20230207",
        },
        {
          status: 0,
          treatmentDt: "20230208",
        },
        {
          status: 1,
          treatmentDt: "20230209",
        },
        {
          status: 0,
          treatmentDt: "20230210",
        },
      ],
    };
  },
  mounted() {
    for (let day of this.cravingStatusList) {
      this.xLabel.push(day.treatmentDt);
      this.yData.push({ y: day.status });
    }
    this.chartInit();
  },
  methods: {
    chartInit() {
      this.gradient = this.$refs.canvas
        .getContext("2d")
        .createLinearGradient(0, 0, 0, 450);
      this.gradient2 = this.$refs.canvas
        .getContext("2d")
        .createLinearGradient(0, 0, 0, 450);

      this.gradient.addColorStop(0, "rgba(255, 0,0, 0.5)");
      this.gradient.addColorStop(0.5, "rgba(255, 0, 0, 0.25)");
      this.gradient.addColorStop(1, "rgba(255, 0, 0, 0)");

      this.gradient2.addColorStop(0, "rgba(0, 231, 255, 0.9)");
      this.gradient2.addColorStop(0.5, "rgba(0, 231, 255, 0.25)");
      this.gradient2.addColorStop(1, "rgba(0, 231, 255, 0)");

      this.renderChart(
        {
          labels: this.xLabel,
          datasets: [
            {
              label: "",
              borderColor: "#FC2525",
              pointBackgroundColor: "#FC2525",
              borderWidth: 1,
              pointBorderColor: "white",
              backgroundColor: this.gradient,
              data: this.yData,
            },
          ],
          options: {
            legend: {
              display: false,
            },
          },
        },
        {
          responsive: true,
          maintainAspectRatio: false,
          legend: {
            display: false,
          },
          scales: {
            xAxes: [
              {
                gridLines: {
                  display: false,
                },
                ticks: {
                  callback: function (value) {
                    return value;
                  },
                  autoSkip: true,
                  maxTicksLimit: 7,
                  stepSize: 0.2,
                  maxRotation: 0,
                  minRotation: 0,
                },
                scaleLabel: {
                  display: true,
                  fontStyle: "bold",
                },
              },
            ],
            yAxes: [
              {
                ticks: {
                  callback: function (value) {
                    return value;
                  },
                  autoSkip: true,
                  maxTicksLimit: 7,
                  stepSize: 0.1,
                  maxRotation: 0,
                  minRotation: 0,
                  min: 0,
                  max: 5,
                },
              },
            ],
          },
          tooltips: {
            callbacks: {
              label: function (tooltipItem, data) {
                const idx = tooltipItem.index;
                const y = data.datasets[0].data[idx].y;
                return `${y}`;
              },
              title: function (data) {
                return `${data[0].xLabel}`;
              },
            },
          },
        }
      );
    },
  },
};
</script>
