<script>
import { Line } from "vue-chartjs";

export default {
  extends: Line,
  props: {
    recallCorrectRate: Array,
  },
  data() {
    return {
      gradient: null,
      gradient2: null,
      xLabel: [],
      yData: [],
    };
  },
  watch: {
    recallCorrectRate() {
      for (let day of this.recallCorrectRate) {
        this.xLabel.push(day.treatmentDt);
        this.yData.push({ y: day.status });
      }
      this.chartInit();
    },
  },
  methods: {
    chartInit() {
      this.gradient = this.$refs.canvas
        .getContext("2d")
        .createLinearGradient(0, 0, 0, 450);
      this.gradient2 = this.$refs.canvas
        .getContext("2d")
        .createLinearGradient(0, 0, 0, 450);

      this.gradient.addColorStop(0, "rgba(11, 0, 255, 0.5)");
      this.gradient.addColorStop(0.5, "rgba(11, 0, 255, 0.25)");
      this.gradient.addColorStop(1, "rgba(11, 0, 255, 0)");

      this.gradient2.addColorStop(0, "rgba(0, 231, 255, 0.9)");
      this.gradient2.addColorStop(0.5, "rgba(0, 231, 255, 0.25)");
      this.gradient2.addColorStop(1, "rgba(0, 231, 255, 0)");

      this.renderChart(
        {
          labels: this.xLabel,
          datasets: [
            {
              label: " ",
              borderColor: "#6F00FF",
              pointBackgroundColor: "#6F00FF",
              borderWidth: 1,
              pointBorderColor: "white",
              backgroundColor: this.gradient,
              data: this.yData,
            },
          ],
          options: {
            legend: {
              display: false,
            },
          },
        },
        {
          responsive: true,
          maintainAspectRatio: false,
          legend: {
            display: false,
          },
          scales: {
            xAxes: [
              {
                gridLines: {
                  display: false,
                },
                ticks: {
                  callback: function (value) {
                    return value;
                  },
                  autoSkip: true,
                  maxTicksLimit: 7,
                  stepSize: 0.2,
                  maxRotation: 0,
                  minRotation: 0,
                },
                scaleLabel: {
                  display: true,
                  fontStyle: "bold",
                },
              },
            ],
            yAxes: [
              {
                ticks: {
                  callback: function (value) {
                    return value;
                  },
                  autoSkip: true,
                  maxTicksLimit: 7,
                  stepSize: 0.1,
                  maxRotation: 0,
                  minRotation: 0,
                  min: 0,
                  max: 100,
                },
              },
            ],
          },
          tooltips: {
            callbacks: {
              label: function (tooltipItem, data) {
                const idx = tooltipItem.index;
                const y = data.datasets[0].data[idx].y;
                return `${y}%`;
              },
              title: function (data) {
                return `${data[0].xLabel}`;
              },
            },
          },
        }
      );
    },
  },
  mounted() {},
};
</script>
