<template>
  <section class="treatment__status__wrap vertical__scroll">
    <div
      class="week__progress__wrap"
      v-if="
        subjectName === ' ' &&
        aStopStatus.stopCheckList.length > 0 &&
        aStopStatus.takingMedicineStatusList.length > 0
      "
    >
      <div
        class="abstinence__progress"
        v-if="aStopStatus.stopCheckList.length > 0"
      >
        <h5></h5>
        <div class="progress__list">
          <dl
            class="raw__week"
            v-for="(week, weekIdx) in aStopStatus.stopCheckList"
            :key="weekIdx"
          >
            <dt>{{ weekIdx + 1 }}</dt>
            <dd>
              <ul>
                <li
                  v-for="(date, dateIdx) in week.days"
                  :key="dateIdx"
                  :class="[
                    { on: date.status === 1 },
                    { no__item: date.dayNum === '' },
                  ]"
                >
                  {{ date.title }}
                </li>
              </ul>
            </dd>
          </dl>
        </div>
        <ul class="state__txt">
          <li>
            <dl>
              <dt> &nbsp;:&nbsp;</dt>
              <dd>{{ aStopStatus.totalAStopDay }}</dd>
            </dl>
          </li>
          <li>
            <dl>
              <dt> &nbsp;:&nbsp;</dt>
              <dd>{{ aStopStatus.continuousAStopDay }}</dd>
            </dl>
          </li>
          <li>
            <dl>
              <dt> &nbsp;:&nbsp;</dt>
              <dd>{{ aStopStatus.longestAStopDay }}</dd>
            </dl>
          </li>
        </ul>
      </div>
      <div
        class="medicine__progress"
        v-if="aStopStatus.takingMedicineStatusList.length > 0"
      >
        <h5></h5>
        <div class="progress__list">
          <dl
            class="raw__week"
            v-for="(week, weekIdx) in aStopStatus.takingMedicineStatusList"
            :key="weekIdx"
          >
            <dt>{{ weekIdx + 1 }}</dt>
            <dd>
              <ul>
                <li
                  v-for="(date, dateIdx) in week.days"
                  :key="dateIdx"
                  :class="[
                    { on: date.status === 1 },
                    { no__item: date.dayNum === '' },
                  ]"
                >
                  {{ date.title }}
                </li>
              </ul>
            </dd>
          </dl>
        </div>
      </div>
    </div>
    <div class="chart__wrap" v-if="subjectName === ' '">
      <div class="chart__item">
        <h5></h5>
        <FelingStatusChart
          :feelingStatusList="aStopStatus.feelingStatusList"
        ></FelingStatusChart>
      </div>
      <div class="chart__item">
        <h5></h5>
        <CravingStatusChart
          :cravingStatusList="aStopStatus.cravingStatusList"
        ></CravingStatusChart>
      </div>
      <div class="chart__item">
        <h5></h5>
        <ExpectationStatusChart
          :expectationStatusList="aStopStatus.expectationStatusList"
        ></ExpectationStatusChart>
      </div>
      <div class="chart__item">
        <h5></h5>
        <WillingnessStatusChart
          :willingnessStatusList="aStopStatus.willingnessStatusList"
        ></WillingnessStatusChart>
      </div>
    </div>
    <div class="chart__wrap mstrong" v-if="subjectName === ' '">
      <div class="chart__item">
        <h5> </h5>
        <QuizCorrectRateChart
          :quizCorrectRate="mStrongStatus.quizCorrectRate"
        ></QuizCorrectRateChart>
      </div>
      <div class="chart__item">
        <h5> </h5>
        <RecallCorrectRateChart
          :recallCorrectRate="mStrongStatus.recallCorrectRate"
        ></RecallCorrectRateChart>
      </div>
    </div>
  </section>
</template>
<script>
import FelingStatusChart from "@/components/PatientDashboard/FelingStatusChart";
import CravingStatusChart from "@/components/PatientDashboard/CravingStatusChart";
import ExpectationStatusChart from "@/components/PatientDashboard/ExpectationStatusChart";
import WillingnessStatusChart from "@/components/PatientDashboard/WillingnessStatusChart";
import QuizCorrectRateChart from "@/components/PatientDashboard/QuizCorrectRateChart";
import RecallCorrectRateChart from "@/components/PatientDashboard/RecallCorrectRateChart";
export default {
  props: {
    subjectName: String,
  },
  watch: {
    subjectName() {
      if (this.$env == "development") console.log(this.subjectName);
    },
  },
  data() {
    return {
      mStrongStatus: {
        quizCorrectRate: [],
        recallCorrectRate: [],
      },
      aStopStatus: {
        stopCheckList: [],
        takingMedicineStatusList: [],
        feelingStatusList: [],
      },
    };
  },
  components: {
    FelingStatusChart,
    CravingStatusChart,
    ExpectationStatusChart,
    WillingnessStatusChart,
    QuizCorrectRateChart,
    RecallCorrectRateChart,
  },
  methods: {
    setMstrongTreatmentStatus(mStrongStatus) {
      this.mStrongStatus = mStrongStatus;
    },
    setAstopTreatmentStatus(aStopStatus) {
      this.aStopStatus.feelingStatusList = "";
      Object.assign(this.aStopStatus, aStopStatus);
      this.aStopStatus.stopCheckList = this.$store.state.treatmentStatusList(
        this.$route.params.startDate
      );
      this.aStopStatus.takingMedicineStatusList =
        this.$store.state.treatmentStatusList(this.$route.params.startDate);
      const stopCheckList = aStopStatus.stopCheckList;
      const takingMedicineStatusList = aStopStatus.takingMedicineStatusList;

      for (let i = 0; i < stopCheckList.length; i++) {
        let weekNum = stopCheckList[i].weekNum;
        for (let j = 0; j < stopCheckList[i].days.length; j++) {
          this.aStopStatus.stopCheckList[weekNum - 1].days.map((obj, idx) => {
            if (
              obj.title === aStopStatus.stopCheckList[weekNum - 1].days[j].title
            ) {
              Object.assign(
                this.aStopStatus.stopCheckList[weekNum - 1].days[idx],
                stopCheckList[i].days[j]
              );
            }
          });
        }
      }

      for (let i = 0; i < takingMedicineStatusList.length; i++) {
        let weekNum = takingMedicineStatusList[i].weekNum;
        for (let j = 0; j < takingMedicineStatusList[i].days.length; j++) {
          this.aStopStatus.takingMedicineStatusList[weekNum - 1].days.map(
            (obj, idx) => {
              if (
                obj.title ===
                aStopStatus.takingMedicineStatusList[weekNum - 1].days[j].title
              ) {
                Object.assign(
                  this.aStopStatus.takingMedicineStatusList[weekNum - 1].days[
                    idx
                  ],
                  takingMedicineStatusList[i].days[j]
                );
              }
            }
          );
        }
      }
    },
  },
};
</script>
