<template>
  <div class="popup__wrap" :class="{ on: popupEventFlag === 'Open' }">
    <div class="popup__bg"></div>
    <div class="common__popup mstrong__popup box__shadow__02">
      <div class="popup__top">
        <h4>  </h4>
        <button class="pop__close__btn" @click="popupCloseClick"></button>
      </div>
      <ul>
        <li>
          <dl>
            <dt class="f__medium"></dt>
            <dd>
              <button
                class="custom__btn"
                @click="prescriptionReset"
                v-if="status === ''"
                :class="{
                  off__btn:
                    patientObj.accessCode === '' ||
                    patientObj.accessCode === null ||
                    patientObj.accessCode === undefined,
                }"
              >
                <p></p>
              </button>
              <ul v-if="status !== ''">
                <li class="calendar__wrap top">
                  <input
                    type="text"
                    class="date__input"
                    :value="prescriptionDateInput"
                    readonly
                  />
                  <span
                    class="calendar__icon"
                    id="calendar01"
                    @click="calendarIconClick"
                  >
                    <img
                      src="@/assets/images/icon/ic-calendar.svg"
                      alt=" "
                    />
                  </span>
                  <VDatePicker
                    @input="clickEndDate($event)"
                    locale="ko"
                    color="gray"
                    :value="prescriptionDateInput"
                    :class="{ active: calendarShow }"
                    is-required
                  />
                </li>
                <li>
                  <button
                    class="custom__btn"
                    @click="prescriptionReset"
                    :class="{
                      off__btn:
                        patientObj.accessCode === '' ||
                        patientObj.accessCode === null ||
                        patientObj.accessCode === undefined,
                    }"
                  >
                    <p></p>
                  </button>
                </li>
              </ul>
            </dd>
          </dl>
        </li>
        <li>
          <dl>
            <dt class="f__medium"></dt>
            <dd>
              <input
                type="text"
                v-model="startDateInput"
                readonly
                class="read__only yellow"
              />
            </dd>
          </dl>
        </li>
        <li>
          <dl>
            <dt class="f__medium"> </dt>
            <dd>
              <select
                name=""
                id=""
                v-model="patientObj.startLevel"
                :disabled="status === ''"
              >
                <option value=""> </option>
                <option
                  :value="idx + 1"
                  v-for="(level, idx) in 12"
                  :key="idx + 1"
                >
                  {{ idx + 1 }}
                </option>
              </select>
            </dd>
          </dl>
        </li>
        <li>
          <dl>
            <dt class="f__medium"> </dt>
            <dd>
              <input
                type="text"
                v-model="patientObj.currentLevel"
                class="read__only"
                readonly
              />
            </dd>
          </dl>
        </li>
        <li>
          <dl>
            <dt class="f__medium"> </dt>
            <dd>
              <select
                name=""
                id=""
                v-model="patientObj.walkingLevel"
                :disabled="status === ''"
              >
                <option value=""> </option>
                <option
                  :value="idx + 1"
                  v-for="(level, idx) in 12"
                  :key="idx + 1"
                >
                  {{ idx + 1 }}
                </option>
              </select>
            </dd>
          </dl>
        </li>
        <li>
          <dl>
            <dt class="f__medium"> </dt>
            <dd>
              <select
                name=""
                id=""
                v-model="patientObj.stretchingLevel"
                :disabled="status === ''"
              >
                <option value=""> </option>
                <option
                  :value="idx + 1"
                  v-for="(level, idx) in 12"
                  :key="idx + 1"
                >
                  {{ idx + 1 }}
                </option>
              </select>
            </dd>
          </dl>
        </li>
      </ul>
      <div class="button__wrap">
        <!-- class="off__btn"   -->
        <button
          class="custom__btn"
          @click="prescriptionPublish"
          :class="{ off__btn: status === '' }"
        >
          <p> </p>
        </button>
        <button
          class="custom__btn"
          :class="{ off__btn: status !== '' }"
          @click="runPrint"
        >
          <p> </p>
        </button>
      </div>
    </div>
  </div>
</template>
<script>
import VDatePicker from "v-calendar/lib/components/date-picker.umd";
import dateParse from "@/utill/dateParse.js";
import {
  addPrescription,
  updatePrescription,
  resetPrescription,
  prescriptionPublish,
  getPatientList,
} from "@/api/patient";
import { getSimpleUserInfo } from "@/utill/auth";

export default {
  data() {
    return {
      educationStartDate: "",
      calendarShow: false,
      patientObj: {
        prescriptionDate: "",
        startDate: "",
        startLevel: "",
        currentLevel: "",
        walkingLevel: "",
        stretchingLevel: "",
        subjectId: "",
      },
      startDateInput: "",
      prescriptionDateInput: "",
      status: "",
      userInfo: {},
    };
  },
  props: {
    popupEventFlag: String,
  },
  mounted() {
    const user = getSimpleUserInfo;
    this.userInfo = user;
  },
  watch: {
    popupEventFlag() {
      this.patientObj.subjectId = this.$route.params.subjectId;
      this.status = this.$route.params.mStrongPrescritionState;
      if (this.status === "") {
        this.patientObj = this.$route.params;
        this.startDateInput = this.patientObj.startDate.replace(
          /(\d{4})(\d{2})(\d{2})/,
          "$1-$2-$3"
        );
      }
    },
  },
  components: {
    VDatePicker,
  },
  methods: {
    runPrint() {
      this.$emit("runPrint", "open");
    },
    popupCloseClick() {
      this.calendarShow = false;
      this.$emit("popupClose", "Close");
    },
    calendarIconClick() {
      this.calendarShow === false
        ? (this.calendarShow = true)
        : (this.calendarShow = false);
    },
    clickEndDate(param) {
      this.patientObj.prescriptionDate = dateParse.dateToString2(param);
      this.prescriptionDateInput = dateParse
        .dateToString2(param)
        .replace(/(\d{4})(\d{2})(\d{2})/, "$1-$2-$3");
      this.calendarShow = false;
      if (
        Number(this.patientObj.prescriptionDate) <
        Number(dateParse.dateToString2(new Date()))
      ) {
        this.$confirm({
          // title: "",
          message: "    .",
          button: {
            yes: "",
          },
        });
        this.patientObj.prescriptionDate = "";
        this.prescriptionDateInput = "";
      }
    },
    prescriptionReset() {
      // this.educationStartDate = "";
      const param = {
        accessCode: this.patientObj.accessCode,
        patientRegNumber: this.patientObj.patientRegNumber,
      };
      this.$confirm({
        // showCancelButton: true,
        title: "  ?",
        message: " ....",
        button: {
          no: "",
          yes: "",
        },
        callback: (confirm) => {
          if (confirm) {
            resetPrescription(param)
              .then((response) => {
                this.$toast.success("  .");
                this.$router.push({
                  name: "PatientInquiry",
                });
                if (this.$env == "development") console.log(response);
              })
              .catch((error) => {
                this.$toast.error(error.message);
                if (this.$env == "development") console.log(error);
              });
          }
        },
      });
    },
    prescriptionPublish() {
      if (this.$env == "development") console.log(this.status);
      const param = {};
      // param.prescriptionDate = this.patientObj.prescriptionDate;
      param.subjectId = this.patientObj.subjectId;
      param.startLevel = String(this.patientObj.startLevel);
      param.stretchingLevel = String(this.patientObj.stretchingLevel);
      param.walkingLevel = String(this.patientObj.walkingLevel);
      param.prescriptionListByWeek = [];
      param.prescriptionDate = null;
      param.startDate = null;
      if (this.patientObj.prescriptionDate === "") {
        this.$toast.error(" ");
        return false;
      }
      if (param.startLevel === "") {
        this.$toast.error("  ");
        return false;
      }
      if (param.walkingLevel === "") {
        this.$toast.error("  ");
        return false;
      }
      if (param.stretchingLevel === "") {
        this.$toast.error("  ");
        return false;
      }
      for (let [key, value] of Object.entries(param)) {
        if (this.$env == "development") console.log(`${key}: ${value}`);
      }
      if (this.status === "") {
        addPrescription(param, this.$route.params.patientRegNumber)
          .then((response) => {
            if (this.$env == "development") console.log(response); //
            const obj = {};
            obj.accessCode = response.data.response.accessCode;
            obj.prescriptionDate = this.patientObj.prescriptionDate;
            if (this.$env == "development") console.log(obj);
            prescriptionPublish(obj, this.$route.params.patientRegNumber)
              .then((response) => {
                if (this.$env == "development") console.log(response);
                this.getPatientInfo(
                  `?doctorInCharge=&centerId=&subjectId=&patientName=${this.$route.params.name}&prescription=&complete=Y`
                );
              })
              .catch((error) => {
                this.$toast.error(error.message);
              });
          })
          .catch((error) => {
            if (this.$env == "development") console.log(error.message);
            this.$toast.error("  ");
          });
      } else if (this.status === "") {
        param.accessCode = this.$route.params.accessCode;
        updatePrescription(param, this.$route.params.patientRegNumber).then(
          (response) => {
            if (this.$env == "development") console.log(response); //
            const obj = {};
            obj.accessCode = param.accessCode;
            obj.prescriptionDate = this.patientObj.prescriptionDate;
            if (this.$env == "development") console.log(obj);
            prescriptionPublish(obj, this.$route.params.patientRegNumber)
              .then((response) => {
                if (this.$env == "development") console.log(response);
                this.getPatientInfo(
                  `?doctorInCharge=&centerId=&subjectId=&patientName=${this.$route.params.name}&prescription=&complete=Y`
                );
              })
              .catch((error) => {
                this.$toast.error(error.message);
              });
          }
        );
      }
    },
    getPatientInfo(searchParams) {
      let patient = {};
      getPatientList(searchParams).then((response) => {
        patient = response.data.response.patientList[0];
        if (patient.sex === "M") {
          patient.sex = "";
        } else {
          patient.sex = "";
        }
        if (patient.birthDay.length == 8) {
          patient.age =
            Number.parseInt(new Date().getFullYear()) -
            Number.parseInt(patient.birthDay.slice(0, 4)) +
            1;
        }
        this.popupCloseClick();
        Object.assign(this.$route.params, patient);
        this.$emit("mStrongPublish", true);
      });
    },
  },
};
</script>
