<template>
  <section class="education__status__wrap vertical__scroll">
    <div class="education__status__title">
      <div>
        <h5></h5>
        <button class="custom__btn" @click="fn_excelExport">
          <span>
            <img
              src="@/assets/images/dashboard/ic-excel.svg"
              alt=" "
            />
          </span>
          <p></p>
        </button>
      </div>
      <ul>
        <li>
          <dl>
            <dt></dt>
            <dd></dd>
          </dl>
        </li>
        <li>
          <dl>
            <dt></dt>
            <dd> </dd>
          </dl>
        </li>
        <li>
          <dl>
            <dt></dt>
            <dd> </dd>
          </dl>
        </li>
        <li>
          <dl>
            <dt></dt>
            <dd></dd>
          </dl>
        </li>
      </ul>
    </div>
    <div class="week__status__wrap">
      <dl v-for="(week, index) in prescriptionListByWeek" :key="week.weekNum">
        <dt>{{ index + 1 }}</dt>
        <dd>
          <ul>
            <li
              v-for="(date, idx) in week.treatmentList"
              :key="idx"
              @click="treatmentPreview(date.dayNum, index + 1)"
              :class="[
                { complete: date.status === 3 },
                { ing: date.status === 2 },
                { no__item: date.id === '' },
              ]"
            >
              {{ getTreatmentDate(7 * (index + 1) - (7 - (idx + 1))) }}
            </li>
          </ul>
        </dd>
      </dl>
    </div>
    <div class="quiz__state__wrap">
      <div class="quiz__state__title">
        <h5></h5>
      </div>
      <div class="graph__wrap">
        <WalkChart :statusByWeek="statusByWeek"></WalkChart>
      </div>
    </div>
  </section>
</template>
<script>
import WalkChart from "@/components/PatientDashboard/WalkChart";
import { jsontoexcel } from "vue-table-to-excel";
import data10 from "@/data/patientManage/data10.json";
export default {
  data() {
    return {
      patientObj: {},
      prescriptionListByWeek: [],
      statusByWeek: [],
      exportExcelGridHead: [
        "",
        "",
        "",
        "",
        "",
        "",
        "",
      ],
    };
  },
  components: {
    WalkChart,
  },
  props: {
    resDashboard: Object,
  },
  watch: {
    resDashboard() {
      this.statusByWeek = [];
      if (this.resDashboard.statusByWeek.length > 0) {
        for (let week of this.resDashboard.statusByWeek) {
          for (let day of week.days) {
            this.statusByWeek.push(day);
          }
        }
      }
    },
  },
  methods: {
    getTreatmentDate(addDate) {
      const startDate = this.$route.params.startDate;
      if (startDate !== null) {
        const startDateParse = new Date(
          startDate.replace(/(\d{4})(\d{2})(\d{2})/, "$1-$2-$3")
        );
        startDateParse.setDate(startDateParse.getDate() + (addDate - 1));
        const month =
          startDateParse.getMonth() + 1 < 10
            ? "0" + String(startDateParse.getMonth() + 1)
            : startDateParse.getMonth() + 1;
        const date =
          startDateParse.getDate() < 10
            ? "0" + String(startDateParse.getDate())
            : startDateParse.getDate();
        return month + "/" + date;
      } else {
        return "";
      }
    },
    setEucationStatus(param) {
      if (this.$route.params.startDate) {
        const startDate = this.$route.params.startDate;
        let year = startDate.slice(0, 4);
        let month = startDate.slice(4, 6);
        month = Number(month) - 1;
        let date = startDate[6] + startDate[7];
        this.prescriptionListByWeek = this.$store.state.prescription(
          new Date(year, month, date).getDay()
        );
      } else {
        this.prescriptionListByWeek = this.$store.state.prescription(0);
      }

      let prescriptionListByWeek;
      prescriptionListByWeek = param.sort(function (a, b) {
        return a.weekNum - b.weekNum;
      });

      for (let i = 0; i < prescriptionListByWeek.length; i++) {
        let weekNum =
          prescriptionListByWeek.length === 1
            ? 0
            : prescriptionListByWeek[i].weekNum - 1;
        for (
          let j = 0;
          j < prescriptionListByWeek[i].treatmentList.length;
          j++
        ) {
          if (prescriptionListByWeek[i].treatmentList[j].id !== null) {
            this.prescriptionListByWeek[weekNum].treatmentList.map(
              (obj, idx) => {
                if (
                  obj.dayNum ===
                  prescriptionListByWeek[weekNum].treatmentList[j].dayNum
                ) {
                  Object.assign(
                    this.prescriptionListByWeek[weekNum].treatmentList[idx],
                    prescriptionListByWeek[weekNum].treatmentList[j]
                  );
                }
              }
            );
          }
        }
      }
    },
    treatmentPreview() {
      if (this.$route.params.issuedYn === "Y") {
        this.$emit("treatmentPreviewLoading", true);

        this.$emit("treatmentPreviewLoading", false);
        this.$emit("treatmentPreview", data10);

        /*this.$toast.error(error.message);
            if (this.$env == "development") console.log(error);
            this.$emit("treatmentPreviewLoading", false);*/
      } else {
        this.$confirm({
          message: "    .",
          button: {
            yes: "",
          },
        });
      }
    },
    fn_excelExport() {
      const prescriptionListByWeek = [];
      for (let i of this.prescriptionListByWeek) {
        for (let j of i.treatmentList) {
          if (j.id !== "" && j.id !== null) {
            prescriptionListByWeek.push(j);
          }
        }
      }

      if (prescriptionListByWeek.length === this.statusByWeek.length) {
        const excelList = [];
        const dayArr = ["", "", "", "", "", "", ""];
        const statusArr = [
          "",
          "",
          "",
          "",
        ];
        for (let i = 0; i < this.statusByWeek.length; i++) {
          const obj = {
            : this.statusByWeek[i].title,
            : dayArr[this.statusByWeek[i].dayNum],
            : prescriptionListByWeek[i].categoryName,
            : prescriptionListByWeek[i].name,
            : statusArr[this.statusByWeek[i].status - 1],
            : this.statusByWeek[i].stepCount,
            : this.statusByWeek[i].distance,
          };
          excelList.push(obj);
        }
        jsontoexcel.getXlsx(
          excelList,
          this.exportExcelGridHead,
          `${this.$route.params.name}   .xls`
        );
      } else {
        this.$toast.error(",     ");
      }
    },
  },
  mounted() {
    this.prescriptionListByWeek = this.$store.state.prescription(0);
  },
};
</script>
